.App {
  font-family: Arial, sans-serif;
  text-align: center;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

h1, h2, h3 {
  color: #333;
}

.status-message {
  color: red;
  font-weight: bold;
}

.login-screen, .character-screen, .game-screen {
  padding: 20px;
  border: 1px solid #eee;
  border-radius: 5px;
  background-color: #fff;
  margin-top: 20px;
}

.form-group {
  margin-bottom: 15px;
  text-align: left;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="password"],
.form-group select {
  width: calc(100% - 20px);
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin: 5px;
  font-size: 16px;
}

button:hover:not(:disabled) {
  background-color: #45a049;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.toggle-auth {
  margin-top: 15px;
  color: #007bff;
  cursor: pointer;
}

.character-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  margin-bottom: 20px;
}

.character-item {
  border: 1px solid #007bff;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
  width: 200px;
  text-align: left;
  transition: background-color 0.2s;
}

.character-item:hover {
  background-color: #e6f2ff;
}

.game-info {
  background-color: #e9e9e9;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 20px;
  text-align: left;
}

.game-log {
  background-color: #333;
  color: #eee;
  padding: 15px;
  border-radius: 5px;
  height: 300px;
  overflow-y: scroll;
  text-align: left;
  font-family: 'Courier New', Courier, monospace;
  font-size: 14px;
  white-space: pre-wrap; /* Preserve whitespace and break lines */
}

.log-entry {
  margin: 0;
  padding: 2px 0;
  border-bottom: 1px dotted #555;
}
.log-entry:last-child {
  border-bottom: none;
}

.game-actions button {
  margin: 8px;
  padding: 12px 20px;
  font-size: 18px;
}

.initializing {
  margin-top: 50px;
  font-size: 1.2em;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border-left-color: #09f;
  animation: spin 1s ease infinite;
  margin: 20px auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
